<template>
    <div style="height: 100%">
        <el-dialog title="冻结用户" :visible.sync="showFreezeDialog" width="250px">
            <el-select v-model="freezeUserForm.freeze_reason" placeholder="请选择冻结理由">
                <el-option
                    v-for="(reason, id) in Consts.freezeReasons"
                    :key="id"
                    :label="reason"
                    :value="id"
                ></el-option>
            </el-select>
            <el-select v-model="freezeUserForm.freeze_time" placeholder="请选择冻结时间">
                <el-option
                    v-for="(time, id) in Consts.freezeTime"
                    :key="id"
                    :label="time"
                    :disabled="time.disable"
                    :value="id"
                ></el-option>
            </el-select>
            <div slot="footer" class="dialog-footer">
                <el-button @click="showFreezeDialog = false">取 消</el-button>
                <el-button
                    :loading="freezeLoading"
                    type="primary"
                    @click="handleFreezeConfirm"
                    :disabled="disableFreezeConfirm"
                >确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="添加用户" :visible.sync="showAddDialog" width="300px">
            <el-form label-position="left" label-width="80px" :model="addUserForm">
                <el-form-item label="用户名">
                    <el-input v-model="addUserForm.username" tabindex="1"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input
                        v-model="addUserForm.password"
                        :type="passwordType"
                        tabindex="2"
                        @keyup.enter.native="handleAddConfirm"
                    ></el-input>
                    <span class="show-pwd" @click="showPwd">
                        <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
                    </span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="showAddDialog = false">取 消</el-button>
                <el-button
                    :loading="addLoading"
                    type="primary"
                    @click="handleAddConfirm"
                    :disabled="disableAddConfirm"
                >确 定</el-button>
            </div>
        </el-dialog>
        <el-table
            v-loading="listLoading"
            :data="userList.list"
            height="95%"
            max-height="100%"
            style="width: 100%"
        >
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="头像">
                            <div class="demo-image__preview">
                                <el-image
                                    style="width: 50px; height: 50px"
                                    :src="props.row.avatar.small"
                                    :preview-src-list="[props.row.avatar.large]"
                                ></el-image>
                            </div>
                        </el-form-item>
                        <el-form-item label="个人描述">
                            <span>{{ props.row.profile }}</span>
                        </el-form-item>
                        <el-form-item label="QQ">
                            <span>{{ props.row.qq }}</span>
                        </el-form-item>
                        <el-form-item label="微信">
                            <span>{{ props.row.wechat }}</span>
                        </el-form-item>
                        <el-form-item label="微博">
                            <span>{{ props.row.weibo }}</span>
                        </el-form-item>
                        <el-form-item label="地区">
                            <span>{{ props.row.address }}</span>
                        </el-form-item>
                        <el-form-item v-if="props.row.unfreeze_time" label="账户解冻时间">
                            <span>{{ props.row.unfreeze_time }}</span>
                        </el-form-item>
                        <el-form-item v-if="props.row.unfreeze_time" label="账户冻结原因">
                            <span>{{ props.row.freeze_reason }}</span>
                        </el-form-item>
                        <el-form-item v-if="props.row.unfreeze_time" label="连续登陆失败次数">
                            <span>{{ props.row.login_failed_times }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column align="center" label="UID" prop="id"></el-table-column>
            <el-table-column align="center" label="头像" prop="avatar">
                <template slot-scope="scope">
                    <div class="block">
                        <el-avatar :size="50" :src="scope.row.avatar.small"></el-avatar>
                    </div>
                </template>
            </el-table-column>
            <el-table-column align="center" label="用户名" prop="username"></el-table-column>
            <el-table-column align="center" label="昵称" prop="nickname"></el-table-column>
            <el-table-column align="center" label="手机号" prop="phone"></el-table-column>
            <el-table-column align="center" label="邮箱" prop="email"></el-table-column>
            <el-table-column align="center" label="上次登录时间" prop="last_login_time"></el-table-column>
            <el-table-column align="center" label="注册时间" prop="register_time"></el-table-column>
            <el-table-column align="center" label="状态">
                <template slot-scope="scope">
                    <el-tag
                        :type="scope.row.unfreeze_time ? 'danger' : 'success'"
                    >{{ scope.row.unfreeze_time ? '冻结' : '正常' }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column align="right">
                <template slot="header" slot-scope="scope">
                    <!-- <el-button
            size="mini"
            type="primary"
                    @click="showAddUser(scope.$index, scope.row)">刷新列表</el-button>-->
                    <el-button
                        size="mini"
                        type="primary"
                        @click="showAddUser(scope.$index, scope.row)"
                    >添加用户</el-button>
                </template>
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="primary"
                        :disabled="scope.row.phone ? false : true"
                        @click="handleResetUser(scope.row.id, scope.$index)"
                    >重置</el-button>
                    <el-button
                        size="mini"
                        type="warning"
                        :loading="freezeLoading"
                        @click="handleFreezeClike(scope.row.id, scope.$index)"
                        v-text="scope.row.unfreeze_time ? '解冻' : '冻结'"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="display: flex">
            <el-pagination
                hide-on-single-page
                background
                layout="prev, pager, next"
                :page-size="userList.limit"
                :page-count="userList.total_page"
                :current-page="userList.page"
                @current-change="getUserList"
                style="margin: auto"
            ></el-pagination>
        </div>
    </div>
</template>

<style>
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
.show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: #889aa4;
    cursor: pointer;
    user-select: none;
}
</style>

<script>
import Consts from '@/config/const'
import { mapGetters } from 'vuex'

const initFreezeForm = () => {
    return {
        uid: 0,
        freeze_reason: undefined,
        freeze_time: undefined
    }
}
const initAddUserForm = () => {
    return {
        username: '',
        password: ''
    }
}
export default {
    created: function() {
        this.getUserList()
    },
    computed: {
        ...mapGetters({
            userList: 'userlist'
        }),
        disableFreezeConfirm: function() {
            return !(
                this.freezeUserForm.freeze_reason &&
                this.freezeUserForm.freeze_time
            )
        },
        disableAddConfirm: function() {
            return !(this.addUserForm.username && this.addUserForm.password)
        }
    },
    methods: {
        showPwd() {
            if (this.passwordType === 'password') {
                this.passwordType = ''
            } else {
                this.passwordType = 'password'
            }
        },
        // 点击添加用户按钮
        showAddUser() {
            // console.log('showAddUser')
            this.showAddDialog = true
        },
        // 重置用户密码
        handleResetUser(uid, index) {
            this.$confirm(
                '此操作将重置该用户的密码, 操作无法撤回, 是否继续?',
                '提示',
                {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }
            )
                .then(() => {
                    this.$store
                        .dispatch('UserManage/resetUser', uid)
                        .then(response => {
                            console.log(response.msg)
                            this.$message({
                                type: 'success',
                                message: response.msg
                            })
                            this.$alert(
                                '新密码: ' + response.data.new_password,
                                '用户密码重置成功'
                            )
                        })
                        .catch(error => {
                            console.log(error)
                        })
                })
                .catch(() => {
                    // this.$message({
                    //   type: 'info',
                    //   message: '已取消删除'
                    // })
                })
        },
        // 显示冻结用户对话框
        showFreezeUserDialog(uid, index) {
            // console.log(index)
            // 拿到当前操作表格的索引号
            this.tableIndex = index
            // this.userList.list[index].unfreeze_time = 99999
            this.showFreezeDialog = true
            this.freezeUserForm.uid = uid
        },
        getUserList(page = 1, limit = 20) {
            this.listLoading = true
            this.$store
                .dispatch('UserManage/UserList', { page: page, limit: limit })
                .then(response => {
                    console.log(response.msg)
                    this.listLoading = false
                })
                .catch(error => {
                    console.log(error)
                    this.listLoading = false
                })
        },
        // 确定添加用户
        handleAddConfirm() {
            if (!this.disableAddConfirm) {
                // console.log('aaa')
                this.addLoading = true
                this.$store
                    .dispatch('UserManage/addUser', this.addUserForm)
                    .then(response => {
                        // Object.assign(this.userList.list[this.tableIndex], this.userList.currentOperateUser)
                        this.addLoading = false
                        this.showAddDialog = false
                        Object.assign(this.addUserForm, initAddUserForm())
                        this.$message({
                            message: response.msg,
                            type: 'success'
                        })
                    })
                    .catch(error => {
                        console.log(error)
                        this.addLoading = false
                        this.showAddDialog = false
                    })
            }
        },
        // 确定冻结用户
        handleFreezeConfirm() {
            if (!this.disableFreezeConfirm) {
                // console.log('aaa')
                this.freezeLoading = true
                this.$store
                    .dispatch('UserManage/freezeUser', this.freezeUserForm)
                    .then(response => {
                        Object.assign(
                            this.userList.list[this.tableIndex],
                            response.data
                        )
                        this.freezeLoading = false
                        this.showFreezeDialog = false
                        Object.assign(this.freezeUserForm, initFreezeForm())
                        this.$message({
                            message: response.msg,
                            type: 'success'
                        })
                    })
                    .catch(() => {
                        this.freezeLoading = false
                        this.showFreezeDialog = false
                    })
            }
        },
        // 点击冻结/解冻按钮
        handleFreezeClike(uid, index) {
            if (uid && this.userList.list[index].unfreeze_time) {
                this.freezeLoading = true
                this.$store
                    .dispatch('UserManage/unfreezeUser', uid)
                    .then(response => {
                        Object.assign(
                            this.userList.list[this.tableIndex],
                            response.data
                        )
                        this.freezeLoading = false
                        this.$message({
                            message: response.msg,
                            type: 'success'
                        })
                    })
                    .catch(error => {
                        console.log(error)
                        this.freezeLoading = false
                    })
            } else {
                this.showFreezeUserDialog(uid, index)
            }
        }
    },
    data() {
        return {
            tableIndex: undefined,
            Consts: Consts,
            showFreezeDialog: false,
            showAddDialog: false,
            passwordType: 'password',
            freezeUserForm: initFreezeForm(),
            addUserForm: initAddUserForm(),
            freezeLoading: false,
            addLoading: false,
            listLoading: true
        }
    }
}
</script>



